@import '../../../styles/colors.scss';

.search {
    width: 100%;
    padding: 0.94rem 0 0.87rem;
    position: relative;
    .search-input {
        display: flex;
        align-items: center;
        height: 1.94rem;
        padding-right: 2.25rem;
        padding-left: 0.94rem;
        border-radius: 2.19rem;
        border: 2px solid #727373;
        width: 100%;
        font-size: 0.56rem;
        background: url('../../../assets/images/search.svg') no-repeat calc(100% - 0.94rem) 50%;

        &::placeholder {
            color: black;
        }
    }

    .list {
        background-color: white;
        min-height: 7.5rem;
        width: 100%;
        box-shadow: rgba(0, 0, 0, 0.24) 0 3px 8px;
        position: absolute;
        bottom: 0;
        right: 0;
        transform: translateY(100%);
        z-index: 50;
        border-radius: 0.5rem;
        padding: 1rem;
        display: flex;
        flex-direction: column;
        gap: 0.25rem;

        &__loading {
            text-align: center;
        }

        &__link {
            text-decoration: none;
            color: black;

            &:hover {
                color: pink;
            }
        }
    }
}
